  },
  computed: {
    getDay: function() {
      this.getDayFun();
      return this.day;
    },
    getHour: function() {
      this.getHourFun();
      return this.hour;
    },
    getMinute: function() {
      this.getMinuteFun();
      return this.minute;
    }
  },
  methods: {
    // 接收data
    getData(data) {
      alert(data);
    },

    getDayFun() {
      let _this = this;

      if (!_this.dayTimer) {
        clearInterval(_this.dayTimer);
        _this.dayTimer = setInterval(function() {
          dayF();
        }, 30000);
      }

      function dayF() {
        // let now = Date.parse(new Date()) / 1000;
        let now = Date.parse(new Date());
        let day = Math.floor((_this.productStatus.deadline - now) / (86400*1000));
        day = day >= 0 ? day : 0;
        _this.day = day;
      }

      dayF();
    },
    getHourFun() {
      let _this = this;

      if (!_this.hourTimer) {
        clearInterval(_this.hourTimer);
        _this.hourTimer = setInterval(function() {
          hourF();
        }, 30000);
      }

      function hourF() {
        // let now = Date.parse(new Date()) / 1000;
        let now = Date.parse(new Date());
        let totalHour = Math.floor((_this.productStatus.deadline - now) / (3600*1000));
        let dayHour =
          Math.floor((_this.productStatus.deadline - now) / (86400*1000)) * 24;
        let hour = totalHour - dayHour;
        if (totalHour < 0) {
          _this.hour = hour;
          return;
        }
        _this.hour = hour;
      }
      hourF();
      // if (totalHour < 0) {
      //   return 0;
      // }
      // return hour;
    },
    getMinuteFun() {
      let _this = this;

      function minuteF() {
        // let now = Date.parse(new Date()) / 1000;
        let now = Date.parse(new Date());
        // let totalMinute = (_this.productStatus.deadline - now) / 60;
        let totalMinute = Math.floor((_this.productStatus.deadline - now)/1000) / 60;
        // let day = Math.floor((_this.productStatus.deadline - now) / 86400);
        let day = Math.floor((_this.productStatus.deadline - now)/1000 / 86400);
        // let hour = Math.floor((_this.productStatus.deadline - now) / 3600) - Math.floor((_this.productStatus.deadline - now) / 86400) * 24;
        let hour = Math.floor((_this.productStatus.deadline - now)/1000 / 3600) - Math.floor((_this.productStatus.deadline - now)/1000 / 86400) * 24;
        if (totalMinute < 0) {
          _this.minute = 0;
          return;
        }
        _this.minute = Math.floor(totalMinute - day * 24 * 60 - hour * 60);
      }

      if (!_this.minuteTimer) {
        clearInterval(_this.minuteTimer);
        _this.minuteTimer = setInterval(function() {
          minuteF();
        }, 30000);
      }

      minuteF();
    }
