<template>
    <div style="width: 100%;">
        <div class="container">
            <!-- 头部产品信息start -->
            <div class="aboutGoods">
                <div class="goods">
                    <img :src="datas.infoImg">
                    <div class="info">
                        <p>{{datas.goodsName}}</p>
                        <span>距离确认信息还剩:</span>
                        <br>
                        <span>{{getHour}} 时 {{getMinute}} 分</span>
                    </div>
                </div>
            </div>
            <!-- 头部产品信息end -->

            <!-- 提示start -->
            <p class="notice">
                申请成功，请在 48 小时内确认信息，逾期将视为主动放弃众测资格
            </p>
            <!-- 提示end -->

            <!-- 编辑确认区域start -->
            <p class="label">申请理由</p>
            <textarea v-if="datas.isEdit" v-model="datas.applyMessage" id="applyMessage"></textarea>
            <textarea v-else v-model="datas.applyMessage" id="applyMessage" readonly></textarea>

            <div class="line"></div>

            <p class="label">收货人姓名</p>
            <input class='inputClass' type="text" placeholder="请填写收件人姓名" :value="datas.userName" readonly>

            <p class="label">收货地址</p>
            <div id="addressMessage">{{datas.address}}</div>

            <p class="label">邮政编码</p>
            <input class='inputClass' type="number" placeholder="请填写收货地址" :value="datas.postCodes" readonly>

            <p class="label">手机号码</p>
            <input class='inputClass' type="number" placeholder="请填写收货地址" :value="datas.tel" readonly>

            <div class="editAddress" v-show="datas.isEdit">编辑收货地址</div>

            <!-- 编辑确认区域end -->

            <!-- 确认按钮start -->
            <div class="verifyBtn" @click='test'>确认</div>
            <!-- 确认按钮end -->
        </div>
    </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      datas: {
        isEdit: true,
        infoImg:
          "http://i4.article.fd.zol-img.com.cn/g5/M00/04/03/ChMkJlpgPuGISF3iAAFxP3ckvFkAAkLfgGYMngAAXFX214.jpg",
        goodsName: "Misfit SHINE 新世界的复古手表",
        deadline: 1525942800000,
        applyMessage:
          "Mavic Air 相比 Mavic Pro 更加圆润，更有层次感，据悉它参考了空气动力学，飞行时可降低风阻。 颜色上，Mavic Air 首发有黑、白、红三色可选",
        userName: "莎啦啦",
        address: "广东省深圳市南山区蛇口网谷万海大厦 C#401dasdasd",
        postCodes: 518000,
        tel: 12345678901
      },
      hourTimer: null,
      hour: 0,
      minuteTimer: null,
      minute: 0
    };
  },
  computed: {
    getHour: function() {
      this.getHourFun();
      return this.hour;
    },
    getMinute: function() {
      this.getMinuteFun();
      return this.minute;
    }
  },
  methods: {
    test() {
      console.log(this.datas.applyMessage);
    },
    // 接收data
    getData(data) {},
    getHourFun() {
      let _this = this;

      if (!_this.hourTimer) {
        clearInterval(_this.hourTimer);
        _this.hourTimer = setInterval(function() {
          hourF();
        }, 30000);
      }

      function hourF() {
        let now = Date.parse(new Date());
        let hour = Math.floor((_this.datas.deadline - now) / (3600 * 1000));
        if (hour < 0) {
          _this.hour = 0;
          return;
        }
        _this.hour = hour;
      }
      hourF();
    },
    getMinuteFun() {
      let _this = this;

      function minuteF() {
        let now = Date.parse(new Date());
        let tempMin = Math.floor((_this.datas.deadline - now) / 1000 / 60);
        let hour = Math.floor((_this.datas.deadline - now) / (3600 * 1000));
        let remain = tempMin - hour * 60;
        if (remain < 0) {
          _this.minute = 0;
          return;
        }
        _this.minute = remain;

        // let now = Date.parse(new Date());
        // let totalMinute = Math.floor((_this.datas.deadline - now) / 1000) / 60;
        // let day = Math.floor((_this.datas.deadline - now) / 1000 / 86400);
        // let hour =
        //   Math.floor((_this.datas.deadline - now) / 1000 / 3600) -
        //   Math.floor((_this.datas.deadline - now) / 1000 / 86400) * 24;
        // if (totalMinute < 0) {
        //   _this.minute = 0;
        //   return;
        // }
        // _this.minute = Math.floor(totalMinute - day * 24 * 60 - hour * 60);
      }

      if (!_this.minuteTimer) {
        clearInterval(_this.minuteTimer);
        _this.minuteTimer = setInterval(function() {
          minuteF();
        }, 1000);
      }

      minuteF();
    }
  },
  mounted() {
    // textare 自适应
    (function() {
      var tx = document.getElementsByTagName("textarea");
      if (tx.length == 0) {
        return;
      }
      for (var i = 0; i < tx.length; i++) {
        tx[i].setAttribute(
          "style",
          "height:" + tx[i].scrollHeight + "px;overflow-y:hidden;"
        );
        tx[i].addEventListener("input", OnInput, false);
      }

      function OnInput() {
        this.style.height = "auto";
        this.style.height = this.scrollHeight + "px";
      }
    })();
  }
};
</script>

<style lang="less" scoped>
@m: 750/375;
/* 主体区域 */
textarea,
input {
  border: 0;
}
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px solid transparent;
  font-size: 14px * @m;
  margin-top: 30px * @m;
  background: #ffffff;
  height: auto;
}

.container .label {
  font-size: 16px * @m;
  color: #b9b9b9;
  margin-bottom: 10px * @m;
}

/* 头部信息 */

.container .aboutGoods .goods {
  height: 114px * @m;
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.8);
  border-radius: 6px * @m;
  margin-bottom: 20px * @m;
  padding: 12px * @m;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
}

.container .aboutGoods .goods img {
  width: 135px * @m;
  height: 90px * @m;
  border-radius: 8px * @m;
  margin-right: 16px * @m;
}

.container .aboutGoods .goods .info p {
  width: 158px * @m;
  font-size: 16px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
  margin-bottom: 10px * @m;
}

.container .aboutGoods .goods .info span {
  font-size: 13px * @m;
  color: #c6c6c6;
}

.container .notice {
  font-size: 13px * @m;
  color: #c6c6c6;
  margin-bottom: 30px * @m;
}

/* 编辑信息区域 */

#applyMessage {
  width: 100%;
  font-size: 16px * @m;
  color: #0f2540;
  margin-bottom: 16px * @m;
  resize: none;
}

#addressMessage {
  /* width: 100%;
        font-size: 16px;
        margin-bottom: 20px;
        resize: none; */
  font-size: 16px * @m;
  margin-bottom: 20px * @m;
}

.line {
  border: 1px solid transparent;
  border-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' height='2px'%3E%3Crect fill='%23EFEFEF' width='100%25' height='50%25'/%3E%3C/svg%3E")
    2 2 stretch;
  margin-bottom: 30px * @m;
}

.inputClass {
  font-size: 16px * @m;
  margin-bottom: 20px * @m;
}

.editAddress {
  width: 120px * @m;
  height: 40px * @m;
  margin-top: 10px * @m;
  background: #e9eef4;
  border-radius: 100px * @m;
  color: #a6b1bf;
  font-size: 14px * @m;
  line-height: 40px * @m;
  text-align: center;
  /* display: none; */
}

/* 确认按钮 */

.verifyBtn {
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3);
  border-radius: 100px * @m;
  width: 100px * @m;
  height: 40px * @m;
  color: #fff;
  line-height: 40px * @m;
  text-align: center;
  margin: 30px * @m auto;
}
</style>



