<template>
    <div style="width: 100%;">
        <div class="container">
            <p class="notice">请申请成功的用户于 {{deadline | dateFormat('YYYY 年 MM 月 DD 日')}} 前提交众测体验报告</p>

            <!-- 名单列表start -->
            <ul class="listData">
                <li v-for="(item,index) in datas" :key="index">
                    <div class="author">
                        <div class="authorIcon">
                            <!-- 不同的睾贵用户用不同的图标 -->
                            <div v-if="item.vipLevel==1" class="vip"></div>
                        </div>
                        <div class="authorInfo">
                            <p>{{item.authorName}}</p>
                            <p>{{item.intro}}</p>
                        </div>
                        <div class="status" v-if="!item.status">已放弃资格</div>
                    </div>
                </li>
            </ul>
            <!-- 名单列表end -->
        </div>

        <!-- 提交按钮start -->
        <div class="verifyBtn">提交报告</div>
        <!-- 提交按钮end -->
    </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      datas: [
        {
          authorName: "不只淡定",
          intro: "旅行作家、前端工程师、搞搞基呵呵呵呵sd",
          vipLevel: 1,
          status:true
        },
        {
          authorName: "不只淡定",
          intro: "旅行作家、前端工程师、搞搞基呵呵呵呵sd",
          vipLevel: 1,
          status:true
        },
        {
          authorName: "不只淡定",
          intro: "旅行作家、前端工程师、搞搞基呵呵呵呵sd",
          vipLevel: 1,
          status:false
        }
      ],
      deadline:new Date()
    };
  },
  methods: {
    // 接收data
    getData(data) {}
  },
  mounted() {}
};
</script>

<style lang="less" scoped>
@m: 750/375;
/* 主体区域 */
.container {
  position: relative;
  width: 100%;
  box-sizing: border-box;
  padding: 0 20px * @m;
  border: 1px solid transparent;
  font-size: 14px * @m;
  margin-top: 30px * @m;
  background: #ffffff;
  height: 565px * @m;
  overflow: auto;
}

.notice {
  font-size: 16px * @m;
  margin-bottom: 20px * @m;
}

/* 确认按钮 */

.verifyBtn {
  position: relative;
  bottom: 0;
  background-image: linear-gradient(-180deg, #9adaff 0%, #61b3ff 100%);
  box-shadow: 0 0 30px * @m 0 rgba(97, 179, 255, 0.3);
  border-radius: 100px * @m;
  width: 100px * @m;
  height: 40px * @m;
  color: #fff;
  line-height: 40px * @m;
  text-align: center;
  margin: 0px auto;
  font-size: 16px * @m;
  margin-top: 10px * @m;
}

/* 名单列表 */

.listData li {
  width: 100%;
  height: 68px * @m;
  box-sizing: border-box;
  padding: 14px * @m;
  margin-bottom: 20px * @m;
  background-color: red;
  background: #ffffff;
  box-shadow: 0 0 30px * @m 0 rgba(227, 227, 227, 0.5);
  border-radius: 6px * @m;
}

.author {
  display: flex;
  align-items: center;
}

.authorIcon {
  position: relative;
  top: 0;
  display: inline-block;
  width: 40px * @m;
  height: 40px * @m;
  border-radius: 100%;
  background-image: url("../assets/image/touxiang.png");
  background-repeat: no-repeat;
  background-size: cover;
}

.authorIcon .vip {
  position: absolute;
  top: 32px * @m;
  width: 32px * @m;
  height: 32px * @m;
  background-image: url("../assets/image/star.png");
  background-repeat: no-repeat;
  background-size: cover;
  left: 50%;
  transform: translateX(-50%);
}

.author .authorInfo {
  flex: 1;
  margin-left: 10px * @m;
  height: 100%;
}

.author .authorInfo p {
  width: 160px * @m;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.author .authorInfo p:nth-child(1) {
  color: #435771;
  font-size: 14px * @m;
  margin-bottom: 6px  * @m;
}

.author .authorInfo p:nth-child(2) {
  font-size: 12px * @m;
  color: #c6c6c6;
}

.author .status {
  font-size: 14px * @m;
  color: #b9b9b9;
  width: 90px * @m;
  text-align: right;
}
</style>



