<style lang='less' scoped>
@m: 750/375;
.recommendArticles li {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  margin-bottom: 20px * @m;
}

.recommendArticles img {
  display: block;
  width: 94px * @m;
  height: 94px * @m;
  border-radius: 8px * @m;
  margin-right: 16px * @m;
}
.articleInfo {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.recommendArticles .articleTitle {
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
  font-size: 16px * @m;
  margin-bottom: 10px * @m;
}
.recommendArticles .author {
  font-size: 12px * @m;
  color: #c6c6c6;
}
</style>



