<!-- 体验报告, 主要用于嵌入详情页的 小图 -->
<template>
    <div>
        <ul class="reportList">
            <li v-for="(item,index) in this.resData" :key="index" @click="enter(item.id)">
                <div class="reportLeft">
                    <img :src="item.imgUrl">
                </div>
                <div class="reportRight">
                    <div class="box">
                        <span class="title">
                            {{item.title}}
                        </span>
                        <span class="author">
                            {{item.author}}
                        </span>
                    </div>
                </div>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  props: ["resData"],
  data() {
    return {};
  },
  methods: {
    enter(id) {
      console.log(id);
    }
  }
};
</script>
<style lang='less' scoped>
/* Bitmap: */
/* 热门众测体验报告区 */
@m: 750/375;
.reportList li {
  position: relative;
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px * @m;
}

.reportList .reportLeft {
  border-radius: 8px * @m;
  width: 94px * @m;
  height: 94px * @m;
  margin-right: 16px * @m;
}
.container .reportList .reportLeft img {
  display: block;
  width: 94px * @m;
  height: 94px * @m;
  border-radius: 8px * @m ;
}

.container .reportList .reportRight {
  position: relative;
  width: 225px * @m;
}

.container .reportList .reportRight .box {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.container .reportList .reportRight .title {
  width: 100%;
  font-size: 16px * @m;
  display: -webkit-box;
  /*! autoprefixer: off */
  -webkit-box-orient: vertical;
  /* autoprefixer: on */
  -webkit-line-clamp: 2;
  overflow: hidden;
  margin-bottom: 10px * @m;
}

.container .reportList .reportRight .author {
  font-size: 12px * @m;
  color: #c6c6c6;
}
</style>




